<script>
import { mapActions, mapGetters } from 'vuex';
import { useRouter, useRoute } from 'vue-router';
import _ from 'lodash';
export default {
    name: "DocumentLibrary",
    components: {
        // PDFUpload,
        // DocumentList
    },
    data() {
        return {
            router: useRouter(),
            oute: useRoute(),
            newPlace: "", // Áî®ÊñºËº∏ÂÖ•Êñ∞Âú∞Èªû
            // places: [], // Âú∞ÈªûÊ∏ÖÂñÆ
            result: null, // ÊäΩÁ±§ÁµêÊûú
            url: '',//Á∂≤ÂùÄ
            history: [], // ÊäΩÁ±§Ê≠∑Âè≤
            blockList:[],
            DadDoesntWant: false,
            DoesntWantEatTooMuch: false,
            DoesntWantEatExpensive: false,
            lastResult: null // ‰∏ä‰∏ÄÊ¨°ÁöÑÊäΩÁ±§ÁµêÊûú
        }
    },
    computed: {
        ...mapGetters('lottery_data', [
            'getPlacesList',
            'getHistory',
            'getNoRepeat'
        ]),
        // Ë®àÁÆótextareaÈ´òÂ∫¶
        places() {
            let filteredList = _.cloneDeep(this.getPlacesList);
            if (this.DadDoesntWant) {
                filteredList = filteredList.filter(place => !place.name.includes('Ë≤¥Êóè‰∏ñÂÆ∂(‰∏≠Â£¢)'));
            }
            if (this.DoesntWantEatTooMuch) {
                filteredList = filteredList.filter(place => !place.name.includes('Ë≤¥Êóè‰∏ñÂÆ∂'));
                filteredList = filteredList.filter(place => !place.name.includes('ÂçÉËëâÁÅ´Èçã'));
                filteredList = filteredList.filter(place => !place.name.includes('Ë•øÂ†§ÁâõÊéí'));
            }
            if (this.DoesntWantEatExpensive) {
                filteredList = filteredList.filter(place => !place.name.includes('ÂçÉËëâÁÅ´Èçã(Á´πÂåó)'));
                filteredList = filteredList.filter(place => !place.name.includes('Ë•øÂ†§ÁâõÊéí'));
            }
            if (this.blockList.length > 0) {
                filteredList = filteredList.filter(place => !this.blockList.includes(place.name));
            }

            return filteredList
        },
    },
    watch: {
    },
    methods: {
        ...mapActions('lottery_data', [
            'updateNewPlace',
            'updateDeletePlace',
            'updateNoRepeat',
        ]),
        addPlace() {
            if (this.newPlace.trim() === "") {
                alert("Ë´ãËº∏ÂÖ•ÊúâÊïàÂú∞ÈªûÔºÅ");
                return;
            }
            this.updateNewPlace(_.cloneDeep(this.newPlace.trim()));
        },
        // removePlace(index) {
        //     this.updateDeletePlace(index);
        // },
        blockPlace(placeName){
            this.blockList.push(_.cloneDeep(placeName));
            console.log(this.blockList);
            
        },
        drawRandomPlace() {
            if (this.places.length === 0) {
                alert("ÁõÆÂâçÊ≤íÊúâ‰ªª‰ΩïÂú∞ÈªûÂèØ‰æõÊäΩÁ±§ÔºÅ");
                return;
            }

            // Èö®Ê©üÈÅ∏ÊìáÂú∞Èªû
            const randomIndex = Math.floor(Math.random() * this.places.length);
            this.result = this.places[randomIndex].name;
            this.url = this.places[randomIndex].url;

            // Ë®òÈåÑÊäΩÁ±§Ê≠∑Âè≤
            const now = new Date();
            const time = `${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}`;
            this.history.unshift({ place: this.result, time });

            // ‰øùÂ≠ò‰∏ä‰∏ÄÊ¨°ÁµêÊûú
            this.lastResult = this.result;
        },
        getHref() {
            if (this.url === '') {
                alert('ÁÑ°ÂÆòÊñπÁ∂≤Á´ô');
                return
            }
            window.open(`${this.url}`, '_blank');
        },
        resetList(){
            this.blockList = [];
            this.DadDoesntWant = false;
            this.DoesntWantEatTooMuch = false;
            this.DoesntWantEatExpensive = false;
        }
    },

    created() {
    },
    mounted() {
    }
};
</script>
<template>
