            <div class="d-flex flex-column mb-2">
                <p>ÁØ©ÈÅ∏Ê¢ù‰ª∂Ôºö</p>
                <div class="d-flex" style="flex-wrap: wrap;">
                    <label class="d-flex justify-content-center align-items-center">
                        <input class="me-2" style="transform: scale(1.5);cursor: pointer;" type="checkbox"
                            v-model="DadDoesntWant" />
                        <span class="me-3" style="flex-shrink: 0;">ËÄÅÁà∏‰∏çÊÉ≥ÈñãËªä</span>
                    </label>
                    <label class="d-flex justify-content-center align-items-center">
                        <input class="me-2" style="transform: scale(1.5);cursor: pointer;" type="checkbox"
                            v-model="DoesntWantEatTooMuch" />
                        <span class="me-3" style="flex-shrink: 0;">‰∏çÊÉ≥ÂêÉÂ§™Â•Ω</span>
                    </label>
                    <label class="d-flex justify-content-center align-items-center">
                        <input class="me-2" style="transform: scale(1.5);cursor: pointer;" type="checkbox"
                            v-model="DoesntWantEatExpensive" />
                        <span class="me-3" style="flex-shrink: 0;">‰∏çÊÉ≥ÂêÉÂæàË≤¥ÁöÑ</span>
                    </label>
                </div>
            </div>

            <div class="d-flex">
                <button class="btn btn-danger me-3" @click="drawRandomPlace" :disabled="places.length === 0">ÊäΩÁ±§</button>
                <!-- <div>
                    <input class="h-100 me-3" type="text" v-model="newPlace" placeholder="Ë´ãËº∏ÂÖ•Âú∞Èªû" />
                    <button class="btn btn-success" @click="addPlace">Ê∑ªÂä†Âú∞Èªû</button>
                </div> -->
            </div>

            <div class="result h-100 pb-3 d-flex flex-column justify-content-center align-items-center" v-if="result">
                <h3 class="p-2" style="font-size: 24px;color: red; user-select: none;">‚ú®ÊäΩÁ±§ÁµêÊûú‚ú®</h3>
                <div style="text-align: center;">
                    <p class="result_name">{{ result }}</p>
                    <p class="result_url"  @click.prevent="getHref()">üîóÈªûÊ≠§ÈñãÂïüÈÄ£Áµê</p>
                </div>
            </div>
        </section>

        <section class="right_area w-50 h-100 d-flex flex-shrink-0">
            <div class="places_list w-50 h-100 flex-shrink-0" style="overflow: auto;">
                <h5 class="p-3 d-flex justify-content-between align-items-center">
