                    <button class="btn btn-success" @click="resetList()">ÈáçÁΩÆ</button>
                </h5>
                <ul class="p-3" style="overflow: auto;">
                    <li class="mb-2 p-2 d-flex justify-content-between align-items-center"
                        v-for="(place, index) in places" :key="index">
                        <p>{{ place.name }}</p>
                        <!-- <button class="btn btn-danger" @click="removePlace(index)">Âà™Èô§</button> -->
                        <button class="btn btn-danger" @click="blockPlace(place.name)">ÁßªÈô§</button>
                    </li>
                </ul>
            </div>
            <div class="history_list w-50 h-100 flex-shrink-0" style="overflow: auto;border-left: 1px solid black;">
                <h5 class="p-3">Ê≠∑Âè≤Á¥ÄÈåÑÔºö</h5>
                <ul class="p-3" style="overflow: auto;">
                    <li v-for="record in history" :key="record.time"
                        class="mb-2 p-2  d-flex justify-content-between align-items-center">
                        {{ record.time }} - {{ record.place }}
                    </li>
                </ul>
            </div>
        </section>

    </section>
</template>